/* Animations */
@-webkit-keyframes rotate360 {
  to {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg); } }
@keyframes rotate360 {
  to {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg); } }

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@-webkit-keyframes pulse {
  0% {
    opacity: 0.2; }
  50% {
    opacity: 0.6; }
  100% {
    opacity: 0.2; } }

@keyframes pulse {
  0% {
    opacity: 0.2; }
  50% {
    opacity: 0.6; }
  100% {
    opacity: 0.2; } }

@-webkit-keyframes meteor {
  0% {
    opacity: 1;
    -webkit-transform: scale(0) translate3d(0, 0, 0);
            transform: scale(0) translate3d(0, 0, 0); }
  15% {
    opacity: 1; }
  25% {
    opacity: 0;
    -webkit-transform: scale(1) translate3d(180px, 0, 0);
            transform: scale(1) translate3d(180px, 0, 0); }
  100% {
    opacity: 0;
    -webkit-transform: scale(0) translate3d(180px, 0, 0);
            transform: scale(0) translate3d(180px, 0, 0); } }

@keyframes meteor {
  0% {
    opacity: 1;
    -webkit-transform: scale(0) translate3d(0, 0, 0);
            transform: scale(0) translate3d(0, 0, 0); }
  15% {
    opacity: 1; }
  25% {
    opacity: 0;
    -webkit-transform: scale(1) translate3d(180px, 0, 0);
            transform: scale(1) translate3d(180px, 0, 0); }
  100% {
    opacity: 0;
    -webkit-transform: scale(0) translate3d(180px, 0, 0);
            transform: scale(0) translate3d(180px, 0, 0); } }

@-webkit-keyframes cursorAnim {
  0%,
  50% {
    opacity: 1; }
  100%,
  50.1% {
    opacity: 0; } }

@keyframes cursorAnim {
  0%,
  50% {
    opacity: 1; }
  100%,
  50.1% {
    opacity: 0; } }

@-webkit-keyframes spaceship {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-12%);
            transform: translateX(-12%); }
  15% {
    opacity: 1; }
  100% {
    -webkit-transform: translateX(135%);
            transform: translateX(135%); } }

@keyframes spaceship {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-12%);
            transform: translateX(-12%); }
  15% {
    opacity: 1; }
  100% {
    -webkit-transform: translateX(135%);
            transform: translateX(135%); } }

@-webkit-keyframes goright {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  50% {
    -webkit-transform: translateX(8px);
            transform: translateX(8px); }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

@keyframes goright {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  50% {
    -webkit-transform: translateX(8px);
            transform: translateX(8px); }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

@-webkit-keyframes goleft {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  50% {
    -webkit-transform: translateX(8px);
            transform: translateX(8px); }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

@keyframes goleft {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  50% {
    -webkit-transform: translateX(8px);
            transform: translateX(8px); }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

@-webkit-keyframes point_to_left {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  to {
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px); } }

@keyframes point_to_left {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  to {
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px); } }



